body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 .App {
  text-align: center;
  margin: 3rem auto;
}

.editorApp {
  float: left;
  width: 45%;
  margin-left: 10%;
}

@media (max-width: 1500px) {
  .editorApp{
   margin: 2rem auto;
   width: 90%;
   align-items: center;
   float: none;
  }

  .App {
    margin: 2rem auto;
    width: 100%;
    text-align: center;
  }
}


.RichEditor-root {
    background: #fff;
    font-family: 'Roboto Mono', monospace;
    font-size: 14px;
    padding: 15px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 5px;
  }
  
  .RichEditor-editor {
    cursor: text;
    font-size: 16px;
    margin-top: 10px;
  }
  
  .RichEditor-editor .public-DraftEditorPlaceholder-root,
  .RichEditor-editor .public-DraftEditor-content {
    margin: 0 -15px -15px;
    padding: 15px;
  }
  
  .RichEditor-editor .public-DraftEditor-content {
    min-height: 100px;
  }
  
  .RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
    display: none;
  }
  
  .RichEditor-editor .public-DraftStyleDefault-pre {
    background-color: rgba(0, 0, 0, 0.05);
    font-family: 'Inconsolata', 'Menlo', 'Consolas', monospace;
    font-size: 16px;
    padding: 20px;
  }
  
  .RichEditor-controls {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    margin-bottom: 5px;
    -webkit-user-select: none;
            user-select: none;
    padding-bottom: 10px;
    border-bottom: 2px solid #7858A6;
  }
  
  .RichEditor-styleButton {
    color: #999;
    cursor: pointer;
    margin-right: 16px;
    padding: 2px 0;
    display: inline-block;
  }
  
  .RichEditor-activeButton {
    color: #7858A6
  }

  .button-container {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 1rem;
  }

  .title {
    font-family: 'Roboto Mono', monospace;
    font-size: 18px;
    font-weight: bold;
  }
.form {
  float: right;
  width: 30%;
  margin-right: 10%;
}

@media screen and (max-width: 1500px) {
  div.form {
    margin: 2rem auto;
    width: 90%;
    float: none;
  }
}

@media screen and (max-width: 550px) {
    .textField-container {
        display: flex;
        flex-direction: column;
    }
  }
.wordList-container {
    overflow-y: scroll;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    max-height: 30rem;
    border-radius: 5px;
    margin-top: 20px;
    font-family: 'Roboto Mono', monospace;
}

